@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo-header {
    background-color: rgb(99, 99, 224) ;
    width: 100%;
    
}

.title {
    text-align: center;
    font-family: 'Roboto Condensed', sans-serif;
    color: white;
}


.movieSearchLabel {
   display: flex;
   justify-content: center;
   font-size: 25px;
}

.userInput {
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.movieBtn {
  padding: 5px 30px;
  margin-top: 14px;
  background-color: #0c26b5;
  color: white;
  font-size: 20px;
  cursor: pointer;
  transition:  .6s;
}

.movieBtn:hover {
    letter-spacing: 7px;
} 
body {
    padding-bottom: 26px;
}
.movieContainer {
    margin-top: 120px;
    box-sizing: border-box;
    background-color: rgb(99, 99, 224);
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    border-radius: 135px;
    padding: 30px;
    box-shadow: 9px 14px 11px;
    /* display: none; */
}

.movieTitle {
    color: white;
    
}

h1 {
    font-family: 'Poppins', sans-serif;
    text-decoration: underline;
    color: white;
    
}

p {
    font-family: 'Poppins', sans-serif;
    color: white;
    font-size: 18px;
}

img {
    padding-bottom: 26px;
}

.movieInfo {
    font-weight: 800;
    font-size: 19px;
    color: white;
    
}

@media only screen and (max-width:862px) {
    .noPoster {
        height: 216px;
        width: 181px;
    }
}

@media only screen and (max-width:500px) {
    .movieContainer {
       border-radius: 0px;
       box-shadow: 5px 22px 25px 6px;
    }
}
.noDisplay {
    position: relative;
    top: 163px;
    display: flex;
    justify-content: center;
    font-size: 40px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.noDisplay {
    position: relative;
    top: 163px;
    display: flex;
    justify-content: center;
    font-size: 40px;
}

@media only screen and (max-width: 499px) {
  .noDisplay {
    position: absolute;
    top: 500px;
    font-size: 28px;
    text-transform: uppercase;
}
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

